import axios from 'axios'
import AxiosConfig from '../axiosConfig.js'

const axiosConfig = new AxiosConfig();
export default class SubscriptionService {
    async getSubscriptionByMember(member) {
        const body = {
            query: `
                query($memberID: Int!){
                    subscriptionsByMember(memberID:$memberID){
                        id
                        amount
                        validityDate
                        subscriptionDate
                    }
                }`,
            variables: {
                memberID: member
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }

    async writeSubscription(subscription) {
        if (subscription.id == null) {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                        mutation($amount: Float!, $validityDate: Date!, $memberCode: Int!){
                        createSubscription(
                            amount: $amount,
                            validityDate: $validityDate,
                            memberCode: $memberCode
                        ){
                            subscription{
                            id
                            amount
                            validityDate
                            subscriptionDate
                            }
                            result
                            message
                        }
                    }`,
                    variables: {
                        amount: subscription.amount,
                        validityDate: subscription.validityDate,
                        memberCode: subscription.member,
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data
        } else {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                        mutation($subscriptionID:Int!, $amount: Float!, $validityDate: Date!, $memberId: Int!){
                        updateSubscription(
                            subscriptionID: $subscriptionID
                            amount: $amount,
                            validityDate: $validityDate,
                            memberId: $memberId
                        ){
                            subscription{
                            id
                            amount
                            validityDate
                            subscriptionDate
                            }
                            result
                            message
                        }
                    }`,
                    variables: {
                        subscriptionID: subscription.id,
                        amount: subscription.amount,
                        validityDate: subscription.validityDate,
                        memberId: subscription.member,
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data
        }
    }

    async deleteSubscription(subscriptionID, member) {
        const body = {
            query: `
                mutation($subscriptionID:Int!, $memberId: Int!){
                    deleteSubscription(
                        subscriptionID: $subscriptionID
                        memberId: $memberId
                    ){
                        message
                        result
                    }
                }`,
            variables: {
                subscriptionID: subscriptionID,
                memberId: member
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
}