<template>
  <div>
    <div class="card card-success card-outline">
      <div class="card-header">
        <h3 class="card-title">Subscription</h3>
        <div class="card-tools">
          <button
            type="button"
            class="btn btn-block btn-success"
            data-toggle="modal"
            data-target="#addSubscription"
          >
            Add Subscription
          </button>
        </div>
      </div>
      <!-- /.card-header -->
      <div class="card-body table-responsive p-0">
        <table class="table table-head-fixed text-nowrap">
          <thead>
            <tr>
              <th>##</th>
              <th>Subscription Date</th>
              <th>Amount</th>
              <th>Validity Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(subscription, index) in subscriptions"
              v-bind:item="subscription"
              :index="index"
              :key="subscription.id"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ subscription.subscriptionDate }}</td>
              <td>{{ subscription.amount }}</td>
              <td>{{ subscription.validityDate }}</td>
              <td>
                <i
                  class="fas fa-edit"
                  style="cursor: pointer"
                  @click="editSubscription(subscription)"
                ></i>
                ¦
                <i
                  class="fas fa-trash"
                  style="cursor: pointer"
                  @click="deleteSubscription(subscription.id)"
                ></i>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- /.card-body -->
    </div>
    <div class="modal fade" id="addSubscription">
      <SubscriptionForm
        @subscriptionCreated="addSubscription"
        :member="member"
        :editingSubscription="editingSubscription"
      >
      </SubscriptionForm>
    </div>
  </div>
</template>
<script>
import SubscriptionService from "./subscriptionService";
import AlertService from "../alertService";
import SubscriptionForm from "./SubscriptionForm.vue";
const subscriptionService = new SubscriptionService();
const alertService = new AlertService();
export default {
  name: "Subscription",
  components: {
    SubscriptionForm,
  },
  props: {
    member: { required: true, type: Number },
  },
  data() {
    return {
      subscriptions: [],
      editingSubscription: null,
    };
  },
  mounted() {
    subscriptionService
      .getSubscriptionByMember(this.member)
      .then((res) => {
        this.subscriptions = res.data.data.subscriptionsByMember;
      })
      .catch((err) => console.error(err));
  },
  methods: {
    addSubscription: function (subscription) {
      if (this.subscriptions.find((n) => n.id === subscription.id)) {
        const index = this.subscriptions.findIndex(
          (n) => n.id === subscription.id
        );
        this.subscriptions.splice(index, 1, subscription);
      } else this.subscriptions.unshift(subscription);
    },
    editSubscription: function (subscription) {
      this.editingSubscription = subscription;
      //$("#addSubscription").modal("show");
    },
    deleteSubscription: function (id) {
      subscriptionService
        .deleteSubscription(id, this.member)
        .then((res) => {
          if (res.data.data.deleteSubscription.result == true) {
            this.subscriptions = this.subscriptions.filter((n) => n.id !== id);
            alertService.showToast(
              res.data.data.deleteSubscription.message,
              "success"
            );
          } else {
            alertService.showToast(
              res.data.data.deleteSubscription.message,
              "error"
            );
          }
        })
        .catch((err) => console.error(err));
    },
  },
};
</script>