<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-8">
          <div class="card card-success card-outline">
            <div class="card-header">
              <div class="card-title">
                <button
                  type="button"
                  class="btn btn-block btn-primary"
                  data-toggle="modal"
                  data-target="#searchMember"
                >
                  <i class="fas fa-search"></i>
                </button>
              </div>
              <div class="card-tools">
                <ul class="nav nav-pills ml-auto">
                  <li class="nav-item">
                    <button
                      type="button"
                      class="btn btn-block btn-success"
                      data-toggle="modal"
                      data-target="#addMemberForm"
                    >
                      Add Member
                    </button>
                  </li>
                </ul>
              </div>
            </div>
            <!-- /.card-header -->
            <div class="card-body table-responsive p-0">
              <table class="table table-head-fixed text-nowrap">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Name</th>
                    <th>Address</th>
                    <th>Phone Number</th>
                    <th>Mail @</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(member, index) in members"
                    v-bind:item="member"
                    :index="index"
                    :key="member.id"
                  >
                    <td>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :id="member.id"
                          @click="checkMember(member, $event)"
                        />
                      </div>
                    </td>
                    <td>{{ member.name }}</td>
                    <td>{{ member.address }}</td>
                    <td>{{ member.phoneNumber }}</td>
                    <td>{{ member.mail }}</td>
                    <td>
                      <i
                        class="fas fa-edit"
                        style="cursor: pointer"
                        @click="editMember(member)"
                      ></i>
                      ¦
                      <i
                        class="fas fa-trash"
                        style="cursor: pointer"
                        @click="deleteMember(member)"
                      ></i>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <!-- /.card-body -->
          </div>
        </div>
        <div class="col-4">
          <MemberCard
            @codeUpdated="updateMemberCode"
            :displayMember="displayMember"
          />
        </div>
      </div>
    </div>
    <div class="modal fade" id="addMemberForm">
      <!-- /.modal-dialog -->
      <MemberForm @memberCreated="addMember" :editingMember="editingMember" />
    </div>
    <div class="modal fade" id="searchMember">
      <div class="modal-dialog">
        <form role="form" @submit.prevent="onSubmit">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title">Member Search</h4>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <StreamBarcodeReader
                v-if="search_by_barcode === true && search_by_qrcode === false"
                @decode="onDecode"
              ></StreamBarcodeReader>
              <qrcode-stream
                v-if="search_by_barcode === false && search_by_qrcode === true"
                @decode="onDecode"
              ></qrcode-stream>
            </div>
            <div class="modal-footer justify-content-between">
              <button
                type="button"
                class="btn btn-default"
                data-dismiss="modal"
              >
                Cancel
              </button>
              <div class="dropdown">
                <button
                  class="btn btn-primary dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Search by {{ search_by }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <a
                    class="dropdown-item"
                    style="cursor: pointer"
                    @click="setSearchBy(true, false, 'Barcode')"
                    >Barcode</a
                  >
                  <a
                    class="dropdown-item"
                    style="cursor: pointer"
                    @click="setSearchBy(false, true, 'Qrcode')"
                    >Qrcode</a
                  >
                </div>
              </div>
            </div>
          </div>
        </form>
        <!-- /.modal-content -->
      </div>
    </div>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import { StreamBarcodeReader } from "vue-barcode-reader";
import { QrcodeStream } from "vue-qrcode-reader";
import MemberService from "./memberService";
import AlertService from "../alertService";
import MemberForm from "./MemberForm.vue";
import MemberCard from "./MemberCard.vue";

const memberService = new MemberService();
const alertService = new AlertService();
export default {
  name: "Member",
  components: { MemberForm, MemberCard, StreamBarcodeReader, QrcodeStream },
  data() {
    return {
      members: [],
      editingMember: null,
      displayMember: null,
      idcheckbox: null,
      search_by: "Barcode",
      search_by_barcode: true,
      search_by_qrcode: false,
    };
  },
  mounted() {
    memberService
      .getAllMembers()
      .then((res) => {
        this.members = res.data.data.members;
        if (this.members.length > 0) {
          this.displayMember = this.members[0];
        }
      })
      .catch((err) => console.error(err));
  },
  methods: {
    addMember: function (member) {
      if (this.members.find((n) => n.id === member.id)) {
        const index = this.members.findIndex((n) => n.id === member.id);
        this.members.splice(index, 1, member);
        //$("#addMemberForm").modal("hide");
      } else this.members.unshift(member);
    },
    updateMemberCode: function (member) {
      const index = this.members.findIndex((n) => n.id === member.id);
      this.members.splice(index, 1, member);
      this.displayMember = member;
    },
    editMember: function (member) {
      this.editingMember = member;
      //$("#addMemberForm").modal("show");
    },
    deleteMember: function (member) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#28a745",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          memberService
            .deleteMember(member)
            .then((res) => {
              if (res.data.data.deleteMember.result == true) {
                this.members = this.members.filter((n) => n.id !== member.id);
                alertService.showToast(
                  res.data.data.deleteMember.message,
                  "success"
                );
              } else {
                alertService.showToast(
                  res.data.data.deleteMember.message,
                  "error"
                );
              }
            })
            .catch((err) => console.error(err));
        }
      });
    },
    checkMember: function (member, event) {
      if (event.target.checked) {
        if (this.idcheckbox != null) {
          document.getElementById(this.idcheckbox).checked = false;
        }
        this.idcheckbox = member.id;
        this.displayMember = member;
      } else {
        document.getElementById(member.id).checked = false;
        this.idcheckbox = null;
      }
    },
    onDecode: function (result) {
      window.location.assign(
        "/member_management/details/" + parseInt(result.slice(0, 12))
      );
    },
    setSearchBy: function (search_by_barcode, search_by_qrcode, search_by) {
      this.search_by_barcode = search_by_barcode;
      this.search_by_qrcode = search_by_qrcode;
      this.search_by = search_by;
    },
  },
};
</script>