<template>
  <div class="modal-dialog">
    <form role="form" @submit.prevent="onSubmit">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">Nouveau Membre</h4>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="member_name">Name</label>
            <input
              type="text"
              class="form-control"
              v-model="member_name"
              id="member_name"
              placeholder="Enter member's Name"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
          <div class="form-group">
            <label for="member_mail">Email address</label>
            <input
              type="email"
              class="form-control"
              v-model="member_mail"
              id="member_mail"
              placeholder="Enter email"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
          <div class="form-group">
            <label for="member_address">Address</label>
            <input
              type="text"
              class="form-control"
              v-model="member_address"
              id="member_address"
              placeholder="Enter member's address"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
          <div class="form-group">
            <label for="member_phoneNumber">Phone Number</label>
            <input
              type="tel"
              class="form-control"
              v-model="member_phoneNumber"
              id="member_phoneNumber"
              placeholder="Enter member's phone number"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
        </div>
        <div class="modal-footer justify-content-between">
          <button
            type="button"
            class="btn btn-default"
            data-dismiss="modal"
            @click="clearFields()"
          >
            Cancel
          </button>
          <button type="submit" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </form>
    <!-- /.modal-content -->
  </div>
</template>

<script>
import MemberService from "./memberService";
import AlertService from "../alertService";
const memberService = new MemberService();
const alertService = new AlertService();
export default {
  name: "MemberForm",
  props: {
    editingMember: Object,
  },
  data() {
    return {
      member_id: null,
      member_name: "",
      member_address: "",
      member_mail: "",
      member_phoneNumber: "",
      member_code: "",
      messageError: [],
    };
  },
  methods: {
    onSubmit: function () {
      const member = {
        id: this.member_id,
        name: this.member_name,
        address: this.member_address,
        mail: this.member_mail,
        phoneNumber: this.member_phoneNumber,
        code: parseInt(this.member_code),
      };

      memberService
        .writeMember(member)
        .then((res) => {
          if (member.id == null) {
            if (res.data.data.createMember.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.createMember.message,
                "success"
              );
              this.$emit("memberCreated", res.data.data.createMember.member);
            } else {
              alertService.showToast(
                res.data.data.createMember.message,
                "error"
              );
            }
          } else {
            if (res.data.data.updateMember.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.updateMember.message,
                "success"
              );
              this.$emit("memberCreated", res.data.data.updateMember.member);
            } else {
              alertService.showToast(
                res.data.data.updateMember.message,
                "error"
              );
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    clearFields: function () {
      this.member_id = null;
      this.member_name = "";
      this.member_address = "";
      this.member_mail = "";
      this.member_phoneNumber = "";
      this.member_code = 0;
    },
  },
  watch: {
    editingMember(member) {
      this.member_id = member.id;
      this.member_name = member.name;
      this.member_address = member.address;
      this.member_mail = member.mail;
      this.member_phoneNumber = member.phoneNumber;
      this.member_code = member.code;
    },
  },
};
</script>

