<template>
  <div class="modal-dialog">
    <form role="form" @submit.prevent="onSubmit">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">Susbscription</h4>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="subscription_amount">Amount</label>
            <input
              type="number"
              class="form-control"
              v-model="subscription_amount"
              id="subscription_amount"
              placeholder="Enter subscription's amount"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
          <div class="form-group">
            <label for="subscription_validityDate">Validity Date</label>
            <input
              type="date"
              class="form-control"
              v-model="subscription_validityDate"
              id="subscription_validityDate"
              placeholder="Enter Validity Date"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
        </div>
        <div class="modal-footer justify-content-between">
          <button type="button" class="btn btn-default" data-dismiss="modal">
            Cancel
          </button>
          <button type="submit" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </form>
    <!-- /.modal-content -->
  </div>
</template>

<script>
import SubscriptionService from "./subscriptionService";
import AlertService from "../alertService";
const subscriptionService = new SubscriptionService();
const alertService = new AlertService();
export default {
  name: "SubscriptionForm",
  props: {
    editingSubscription: { type: Object },
    member: { required: true, type: Number },
  },
  data() {
    return {
      subscription_id: null,
      subscription_amount: 0.0,
      subscription_validityDate: "",
      messageError: [],
    };
  },
  methods: {
    onSubmit: function () {
      const subscription = {
        id: this.subscription_id,
        amount: this.subscription_amount,
        validityDate: this.subscription_validityDate,
        member: this.member,
      };

      subscriptionService
        .writeSubscription(subscription)
        .then((res) => {
          if (subscription.id == null) {
            if (res.data.data.createSubscription.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.createSubscription.message,
                "success"
              );
              this.$emit(
                "subscriptionCreated",
                res.data.data.createSubscription.subscription
              );
            } else {
              alertService.showToast(
                res.data.data.createSubscription.message,
                "error"
              );
            }
          } else {
            if (res.data.data.updateSubscription.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.updateSubscription.message,
                "success"
              );
              this.$emit(
                "subscriptionCreated",
                res.data.data.updateSubscription.subscription
              );
            } else {
              alertService.showToast(
                res.data.data.updateSubscription.message,
                "error"
              );
            }
          }
        })
        .catch((err) => {
          console.log(err);
          /*let i;
          for (i = 0; i < err.response.data["violations"].length; i++) {
            this.messageError.push(err.response.data["violations"][i].message);
          }
          $(".toast").toast("show");*/
        });
    },
    clearFields: function () {
      this.subscription_id = null;
      this.subscription_amount = 0.0;
      this.subscription_validityDate = "";
      this.messageError = [];
    },
  },
  watch: {
    editingSubscription(subscription) {
      this.subscription_id = subscription.id;
      this.subscription_amount = subscription.amount;
      this.subscription_validityDate = subscription.validityDate;
    },
  },
};
</script>

