<template>
  <div class="card card-success card-outline">
    <div class="card-header">
      <div class="card-tools">
        <button
          type="button"
          class="btn btn-outline-success"
          @click="locationRedirection"
        >
          <i class="fas fa-eye"></i> Details
        </button>
      </div>
    </div>
    <!-- /.card-header -->
    <div class="card-body box-profile" v-if="displayMember != null">
      <div class="text-center">
        <img
          class="img-fluid"
          :src="'/media/qr_codes/qr_code-' + displayMember.code + '.png'"
        />
      </div>
      <div>
        <h3 class="profile-username text-center">{{ displayMember.name }}</h3>

        <p class="text-muted text-center">{{ displayMember.mail }}</p>

        <ul class="list-group list-group-unbordered mb-3">
          <li class="list-group-item">
            <b>Address</b>
            <a class="float-right">{{ displayMember.address }}</a>
          </li>
          <li class="list-group-item">
            <b>Phone Number</b>
            <a class="float-right">{{ displayMember.phoneNumber }}</a>
          </li>
          <li class="list-group-item">
            <img
              class="img-fluid"
              :src="'/media/barcodes/barcode-' + displayMember.code + '.png'"
            />
          </li>
        </ul>
        <button
          href="#"
          class="btn btn-warning btn-block"
          @click="resetBarcode(displayMember)"
        >
          <b>Edit Barcode</b>
        </button>
      </div>
    </div>
    <!-- /.card-body -->
  </div>
</template>

<script>
import Swal from "sweetalert2";
import MemberService from "./memberService";
import AlertService from "../alertService";
const memberService = new MemberService();
const alertService = new AlertService();
export default {
  name: "MemberCard",
  props: {
    displayMember: Object,
  },
  data() {
    return {
      member_id: null,
      member_name: "",
      member_address: "",
      member_mail: "",
      member_phoneNumber: "",
      member_code: "",
      messageError: [],
    };
  },
  methods: {
    locationRedirection: function () {
      window.location.assign("/member_management/details/" + this.member_code);
    },
    resetBarcode: function (member) {
      Swal.fire({
        title: "Are you sure ?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#28a745",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, reset the Barcode",
      }).then((result) => {
        if (result.isConfirmed) {
          memberService
            .resetMemberCode(member)
            .then((res) => {
              if (res.data.data.resetMemberCode.result == true) {
                alertService.showToast(
                  res.data.data.resetMemberCode.message,
                  "success"
                );
                this.$emit("codeUpdated", res.data.data.resetMemberCode.member);
              } else {
                alertService.showToast(
                  res.data.data.resetMemberCode.message,
                  "error"
                );
              }
            })
            .catch((err) => console.error(err));
        }
      });
    },
  },
  watch: {
    displayMember(member) {
      this.member_id = member.id;
      this.member_name = member.name;
      this.member_address = member.address;
      this.member_mail = member.mail;
      this.member_phoneNumber = member.phoneNumber;
      this.member_code = member.code;
    },
  },
};
</script>

