import axios from 'axios'
import AxiosConfig from '../axiosConfig.js'

const axiosConfig = new AxiosConfig();
export default class MemberService {
    async getAllMembers() {
        const data = await axios({
            method: "POST",
            url: axiosConfig.getbaseURL(),
            data: {
                query: `{ 
                    members {
                        id
                        name
                        phoneNumber
                        mail
                        code
                        address
                    }
                }`
            }
        });
        return data
    }

    async writeMember(member) {
        if (member.id == null) {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                        mutation ($name: String!, $phoneNumber: String!, $address: String!, $mail: String!) {
                        createMember (
                            name: $name,
                            phoneNumber: $phoneNumber,
                            address: $address,
                            mail: $mail
                        ){
                            member {
                                id
                                name
                                phoneNumber
                                address
                                mail
                                code
                            }
                            message
                            result
                        } 
                    }`,
                    variables: {
                        name: member.name,
                        phoneNumber: member.phoneNumber,
                        address: member.address,
                        mail: member.mail
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        } else {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                        mutation ($memberID: Int!, $name: String!, $phoneNumber: String!, $address: String!, $mail: String!) {
                        updateMember (
                            memberID: $memberID,
                            name: $name,
                            phoneNumber: $phoneNumber,
                            address: $address,
                            mail: $mail
                        ){
                            member {
                                id
                                name
                                phoneNumber
                                address
                                mail
                                code
                            }
                            message
                            result
                        } 
                    }`,
                    variables: {
                        memberID: member.id,
                        name: member.name,
                        phoneNumber: member.phoneNumber,
                        address: member.address,
                        mail: member.mail
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        }
    }

    async deleteMember(member) {
        const body = {
            query: `
                mutation($memberId:Int!, $mail: String!){
                    deleteMember(
                        memberId: $memberId,
                        mail: $mail
                    ){
                        message
                        result
                    }
                }`,
            variables: {
                memberId: member.id,
                mail: member.mail
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
    async resetMemberCode(member) {
        const body = {
            query: `
                mutation($memberId:Int!, $mail: String!){
                    resetMemberCode(
                        memberId: $memberId
                        mail: $mail
                    ){
                        message
                        result
                        member{
                            id
                            name
                            phoneNumber
                            address
                            mail
                            code
                        }
                    }
                }`,
            variables: {
                memberId: member.id,
                mail: member.mail
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
}